<template>
    <div>
<nav-bar :name="title"></nav-bar>
        <v-spacer></v-spacer> 
        <v-card class="ma-1" cols="12">
        <v-container>
            <v-row>
                <v-col cols="12">
                    <h5>*File:</h5>
                <v-file-input
                class="mb-n5"
                label="Choose file"
                outlined
                dense
                v-model="inv.file"
                ></v-file-input>
                </v-col>
                <v-col
                    cols="12"
                    >
                    <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="false"
                        :return-value.sync="inv.due_date"
                        transition="scale-transition"
                        offset-y
                        min-width="auto"
                    >
                        <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                            class="mb-n5"
                            v-model="inv.due_date"
                            prepend-inner-icon="mdi-calendar"
                            outlined
                            dense
                            v-bind="attrs"
                            v-on="on"
                        ></v-text-field>
                        </template>
                        <v-date-picker
                        v-model="inv.due_date"
                        no-title
                        scrollable
                        >
                        <v-spacer></v-spacer>
                        <v-btn
                            text
                            color="primary"
                            @click="menu = false"
                        >
                            Cancel
                        </v-btn>
                        <v-btn
                            text
                            color="primary"
                            @click="$refs.menu.save(inv.due_date)"
                        >
                            OK
                        </v-btn>
                        </v-date-picker>
                    </v-menu>
                    </v-col>
                <v-col cols="12">
                    <h5>*Description:</h5>
                <v-text-field
                class="mb-n5"
                outlined
                dense
                v-model="inv.description"
                ></v-text-field>
                </v-col>
            </v-row>
            <v-col>
            <v-btn
            class="mx-4 my-2"
            color="#989898"
            dark
            @click="$router.go(-1)"
            >
            GO BACK
            </v-btn>
            <v-btn
            class="mx-4 my-2 primary"
            light
            :loading="loading"
            @click="add_invoice"
            >
            ADD INVOICE
            </v-btn>
            </v-col>
        </v-container>
        </v-card>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import InvoiceService from '@/services/InvoiceService';
import Swal from 'sweetalert2'

export default {
    components: {
      NavBar
    },

    data: () => ({
        title: 'ADD INVOICE',
        loading: false,
        inv: {
            file: null,
            due_date: new Date().toISOString().substr(0, 10),
            description: ''
        },
        menu: false,
    }),

    methods:{
      add_invoice(){
        this.loading = true
        InvoiceService
          .add_invoice(this.inv)
          .then(() => {
            Swal.fire({
                  icon: 'success',
                  title: 'Success',
                  text: 'The invoice successfully added.'
              }).then(() => {
                  this.loading = false
                  this.$router.go(-1)
              })
          })
          .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
      }
    }
}
</script>